.loader {
  height: 100%;
}

.pl-loader {

  position: fixed;
  top:50%;
  left:50%;
  margin-top: -0.5em;
  margin-left: -0.5em;

  font-size: 4em;
  position: relative;
  height: 1em;
  width: 1em;
  color: black;
  display: inline-block;
  text-align: center;

  .symbol {

    position: absolute;
    top: 0;
    left: 0;

    &:before,
    &:after,
    span:before {
      font-family: "pllogo";
      font-style: normal;
      font-weight: normal;
      speak: none;
      display: inline-block;
      text-decoration: inherit;
      width: 1em;
      margin: 0;
      text-align: center;
      font-variant: normal;
      text-transform: none;
      line-height: 1em;

      position: absolute;
      top: 0;
      left: 0;
    }

    &:before {
      content: '\e804';
      @include animation('pulse3 2s infinite');
    }

    span:before {
      content: '\e803';
      @include animation('pulse2 2s infinite');
    }

    &:after {
      content: '\e802';
      @include animation('pulse1 2s infinite');
    }

    &:before,
    &:after,
    span:before {
      color: white;
      opacity: 0.6;
    }

  }
  .text {

    font-size: 1em;
    font-style: normal;

    position: absolute;
    top: 0;
    left: 1.1em;

    &:before,
    &:after {
      font-size: 0.35em;
      position: absolute;
      left: 0;
    }

    &:before {
      font-family: 'Sanchez Slab';
      content: 'Performance';
      font-weight: 500 !important;
      top: 0.4em;
    }
    &:after {
      font-family: 'Sanchez Slab';
      font-weight: 200 !important;
      content: 'Learning';
      top: 1.4em;
    }

  }

  .message {
    font-size: 0.2em;
    font-style: normal;
    position: absolute;
    text-align: center;
    width: 10em;
    display: inline-block;
    top: 6em;
    left: -2.5em;
    color: white;
  }

  &.loading {
    .symbol {
      span:after {
        content: 'Loading';
        font-size: 0.2em;
        font-style: normal;
        top: 2.2em;
        position: relative;
        text-align: center;
        width: 5em;
        display: inline-block;
      }
    }
  }

}

@include keyframes(pulse1) {
  0% {    color: transparent;  }
  35% {   color: white;  }
  70% {  color: transparent;  }
  100% {  color: transparent;  }
}

@include keyframes(pulse2) {
  0% {    color: transparent;  }
  10% {   color: transparent;  }
  45% {   color: white;      }
  80% {   color: transparent;  }
  100% {  color: transparent;  }
}

@include keyframes(pulse3) {
  0% {    color: transparent;  }
  20% {   color: transparent;  }
  55% {   color: white;      }
  90% {   color: transparent;  }
  100% {  color: transparent;  }
}

