.frame-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding-top: 80px;
    overflow: hidden;
    .frame-inner {
        overflow: auto;
        height: 100%;
        padding-bottom: 10px;
    }

    // These are temporary development classes
    &.frame-red {
        background: #835365;
    }
    &.frame-green {
        background: #1E8978;
    }
    &.frame-blue {
        background: #0F769F;
    }
    &.frame-greyish {
        background: #628788;
    }
    &.frame-brightgreen {
        background: #15A674;
    }
}

.frame-full {
    height: 100%;
}