$duration: 0.5s;

.animate {
    transform: translate3d(0, 0, 0);
    display: block;
    overflow-x: hidden;
    height: 100%;
    -webkit-overflow-scrolling: touch;
.animate-page {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    > div {
        min-height: 100%;
        padding: 1em;
    }
    transition: transform $duration ease;
    transform: translate3d(0, 0, 0);
    &.ng-enter {
         transform: translate3d(0, 100%, 0);
         z-index: 0;
     }
    &.ng-enter-active {
        transform: translate3d(0, 0, 0);
    }
    &.ng-leave {
        transform: translate3d(0, 0, 0);
    }
    &.ng-leave-active {
        transform: translate3d(0, -100%, 0);
        z-index: -10;
    }
    .animate-back {
        &.ng-enter {
            transform: translate3d(0, -100%, 0);
            z-index: -10;
        }
        &.ng-enter-active {
            transform: translate3d(0, 0, 0);
        }
        &.ng-leave {
            transform: translate3d(0, 0, 0);
        }
        &.ng-leave-active {
            transform: translate3d(0, 100%, 0);
            z-index: 0;
        }
    }
}

}